<template>
  <div>
      <h1>LocalisationDetail</h1>
        <div class="card">
            <div>
                <label>id:</label>{{localisation.localisation_id}}
            </div>            
            <div>
                <label>Pays:</label>{{localisation.pays}}
            </div>
            <div>
                <label>Région:</label>{{localisation.region}}
            </div>
            <div>
                <label>Département:</label>{{localisation.departement}}
            </div>
            <div>
                <label>Arrondissement:</label>{{localisation.arrondissement}}
            </div>
            <b-button pill variant="success" @click="back()">Back</b-button>
            <b-button pill variant="primary" @click="localisationEdit(localisation)">Edit</b-button> 
            <b-button pill variant="danger" @click="localisationDelet(localisation)">Delet</b-button>
     </div>
  </div>
</template>

<script>
export default {
    name:'LocalisationDetail',

    data(){
        return {
            localisation: this.$store.getters.getDetailLocalisation
        }
    },

    methods: {
        back(){
            //alert(this.$store.getters.getDetailLocalisation.pays);
            this.$router.push("/localisation-list");
        },
        localisationEdit(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            this.$router.push("/localisation-edit/");
        },
        localisationDelet(localisation){
            this.$store.commit('setDetailLocalisation',localisation);
            //this.$router.push("/localisation-edit/");
        }
    }
}
</script>

